body {
  color: #fff;
  background-color: #925dce;
  background-image: linear-gradient(rgba(0, 0, 0, .18), rgba(0, 0, 0, .18)), url("background.9542d45e.png");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: El Messiri, sans-serif;
}

input {
  outline: rgba(0, 0, 0, 0);
  font-family: inherit;
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

button {
  cursor: pointer;
  color: currentColor;
  border: none;
  font-family: inherit;
}

* {
  margin-top: 0;
  margin-bottom: 0;
}

.container {
  min-width: 320px;
  max-width: 428px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 16px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1158px;
    padding: 0 15px;
  }
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

:root {
  --mainText: #fff;
  --secondaryText: #925dce;
  --purple: #f200ff;
  --brownText: #542502;
  --background: rgba(58, 46, 141, .68);
  --backgroundExample: rgba(145, 77, 227, .68);
}

.header-page {
  width: 100%;
  z-index: 2;
  background: rgba(0, 0, 0, .35);
  margin: 0 auto;
  position: fixed;
}

.header-container {
  border-bottom: 2.5px solid #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  transition: transform .3s ease-in-out;
}

.header-logo:hover, .header-logo:focus {
  transform: scale(1.1);
}

.header-list {
  display: none;
}

.header-link {
  text-align: center;
  letter-spacing: .02em;
  color: var(--mainText);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.07;
  transition: color .3s ease-in-out;
}

.header-link:hover, .header-link:focus {
  color: var(--purple);
}

.menu-toggle {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.burger-menu {
  fill: var(--mainText);
  transition: fill .3s ease-in-out;
}

.burger-menu:hover, .burger-menu:focus {
  fill: var(--purple);
}

.header-btn {
  width: 120px;
  height: 35px;
  background-color: var(--mainText);
  color: var(--purple);
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  transition: background-color .3s ease-in-out;
  display: none;
}

.header-btn:hover, .header-btn:focus {
  background-color: var(--purple);
  color: var(--mainText);
}

@media screen and (min-width: 768px) {
  .menu-toggle {
    display: none;
  }

  .header-list {
    align-items: center;
    gap: 28px;
    display: flex;
  }

  .header-btn {
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .header-link {
    font-size: 20px;
    font-weight: 700;
  }

  .header-list {
    gap: 50px;
  }

  .header-btn {
    width: 140px;
    height: 45px;
    font-size: 20px;
    font-weight: 600;
  }
}















